import React from 'react'
import { AppBar, Toolbar, IconButton, Badge, Menu, MenuItem, Typography} from '@material-ui/core'
import { ShoppingCart } from '@material-ui/icons'
import useStyles from './styles'

function Navbar() {
    const classes = useStyles()

    return (
        <>
            <AppBar position='fixed' className={classes.appBar} color='inherit'>
                <Toolbar>
                    <Typography variant='h6' className={classes.title} color='inherit'>
                    <svg xmlns="http://www.w3.org/2000/svg" style={{height: '35px'}} fill="none" viewBox="0 0 24 24" stroke="Gray">
                        <path strokeLinecap="round" strokeLinejoin="round" strokeWidth={2} d="M8 9l3 3-3 3m5 0h3M5 20h14a2 2 0 002-2V6a2 2 0 00-2-2H5a2 2 0 00-2 2v12a2 2 0 002 2z" /></svg>
                        Developer-Store
                    </Typography>
                    <div className={classes.grow} />
                    <div className={classes.button}>
                        <IconButton aria-label='Show cart items' color='inherit'>
                            <Badge badgeContent={2} color='secondary'>
                                <ShoppingCart />
                            </Badge>
                        </IconButton>
                    </div>
                </Toolbar>
            </AppBar>
        </>
    )
}

export default Navbar
